* {
  margin: 0;
}

html, body {
  height: 100%;
  overflow-x: hidden;
}

#root {
  height: 100%;
}

.App {
  padding: 0;
  background-color: #F9F9F9;
  height: 100%;
}

.App-header-wrap {
  padding-bottom: 4px;
  box-shadow: 0 3px 12px rgb(98 107 181 / 25%);
}

.App-header {
  line-height: 80px;
  color: #000;
  font-size: 42px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 80px;
}
.App-header-import {
  width: 160px;
  height: 80px;
  margin-right: 18px;
  background: url('./img/bg.png') 0 0 no-repeat;
  background-size: cover;
}
.App-header-sub {
  width: calc(100% - 160px);
  font-size: 24px;
  color: #666;
  height: 80px;
  line-height: 88px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.App-main {
  box-sizing: border-box;
  height: 100%;
  overflow-y: auto;
}

.App-main-inner { 
  margin-top: 6px;
  height: 442px;
}

@media (max-width: 1253px) {
  .App-main {
      width: 1253px;
  }
  .App-main-inner,
  .App-header {
      width: 967px;
      margin: 0 143px;
  }
  .App-main-inner {
    margin-top: 36px;
    height: 442px;
  }
}

@media (min-width: 1254px) and (max-width: 1506px) {
  .App-main-inner,
  .App-header  {
      margin: 0 143px;
      width: auto;
  }
  .App-main-inner {
    margin-top: 26px;
  }
}

@media (min-width: 1600px){
  .App-main-inner,
  .App-header  {
      margin: 0 auto;
      width: 1400px;
  }
  .App-main-inner {
    margin-top: 26px;
  }
}

.App-trans-operation-wrapper {
  height: 40px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.App-left-selet {
  margin: 0;
}

.App-select-icon-wrap {
  height: 30px;
  line-height: 30px;
  padding: 0 24px;
}
.App-select-icon {
  background-image: -webkit-image-set(url(https://fanyi-cdn.cdn.bcebos.com/webStatic/translation/asset/exchange1x.856b2112.png) 1x,url(https://fanyi-cdn.cdn.bcebos.com/webStatic/translation/asset/exchange2x.4b71fe36.png) 2x);
  background-position: -2px 50%;
  background-repeat: no-repeat;
  display: inline-block;
  height: 21px;
  width: 21px;
  margin-top: 4px;
  
}

.App-right-selet {
  margin: 0;
}

.App-submit {
  margin-left: 21px;
}

.App-translateio {
  width: 100%;
  margin-top: 10px;
  background-color: #F0F0F0;
  min-height: 180px;
  display: flex;
  justify-content: space-between;
}

.App-trans-left {
  width: 33.33333%;
  min-height: 390px;
  position: relative;
}

.App-trans-input-wrap {
  background-color: #fff;
  border: 1px solid #dedede;
  position: relative;
  height: 390px;
  box-sizing: border-box;
  transition: all .1s linear;
}

.App-trans-input-wrap-active {
  border: 1px solid #4395FF;
}

.App-input-wrap {
  height: 390px;
  position: relative;
}

.App-textarea-wrap {
  min-height: 390px;
  margin-right: 35px;
  position: relative;
}

.App-textarea-wrap-inner,
.App-trans-right-text-wrap {
  width: 100%;
  height: 388px;
  overflow-y: auto;
}
.App-trans-right-text-wrap {
  overflow-x: hidden;
}

.App-textarea,
.App-trans-right-text {
  width: calc(100% - 20px);
  min-height: 362px;  
  margin: 10px;
  border: none;
  outline: none;
  resize: none;
  color: #333;
  overflow: hidden;
  font-size: 15px;
  line-height: 32px;
  box-sizing: border-box;
}

.App-trans-right-text {
  margin: 11px  12px;
  overflow-y: auto;
}

.App-clear-btn {
  position: absolute;
  right: 8px;
  top: 16px;
  width: 24px;
  height: 24px;
}

.App-trans-right,
.App-trans-middle {
  width: 33.33333%;
  min-height: 390px;
  position: relative;
}

.App-trans-right-text {
  word-wrap: break-word;
} 

.App-trans-middle {
  background-color: #E8DAEF;
  border: 1px solid #E4D5E5;
}

.App-trans-right {
  background-color: #f0f0f0;
  border: 1px solid #dedede;
}

